import { Injectable } from "@angular/core";

@Injectable()
export class TokenService {
  constructor() {}

  getToken(): string {
    if (JSON.parse(localStorage.getItem("currentUser"))) {
      return JSON.parse(localStorage.getItem("currentUser")).token;
    }
    return null;
  }
}



