export class Area {
  areaId: string;
  areaName: string;
  cities: Array<string>;
  state: string;
  country: string;

  constructor() {
    this.areaId = "";
    this.areaName = "";
    this.cities = [];
    this.state = "";
    this.country = "";
  }
}



