import { Injectable, NgZone } from "@angular/core";
import { environment } from "../../../environments/environment";
import { Observable } from "rxjs/Observable";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { HttpClient } from "@angular/common/http";
import "rxjs/add/operator/share";
import { Notification } from "app/shared/notification/notification";
declare const Pusher: any;

@Injectable()
export class NotificationService {
  public baseUrl = environment.corona.NOTIFICATION_BASE_URL;
  pusher: any;
  messagesChannel: any;
  private tempNotifications: Array<Notification>;
  public notifications: BehaviorSubject<Array<Notification>>;

  constructor(private http: HttpClient, private ngZone: NgZone) {
    this.pusher = new Pusher(environment.corona.pusher.KEY, {
      encrypted: true
    });

    this.notifications = new BehaviorSubject([]);
    this.messagesChannel = this.pusher.subscribe("client-messages-channel");
    this.tempNotifications = [];
  }

  getNotifications(id) {
    this.http.get(this.baseUrl + "/notifications?recipientId=" + id).subscribe(obj => {
      this.addNotification(obj["data"]);
    });

    this.messagesChannel.bind(`client_${id}_message`, message => {
      this.addNotification(message);
    });
  }

  readNotification(notification) {
    if (!notification.isRead) {
      this.http.patch(this.baseUrl + `/notification/${notification.notificationId}`, { isRead: true }, { observe: "response" }).subscribe(data => {
        this.clearNotifications();
        this.getNotifications(this.getUserId());
      });
    }
  }

  getUserId() {
    if (localStorage.getItem("currentUser")) {
      return JSON.parse(localStorage.getItem("currentUser")).userId;
    }
  }

  addNotification(message) {
    if (Array.isArray(message)) {
      this.tempNotifications.push(...message);
    } else {
      this.tempNotifications.push(message);
    }
    this.publishNotifications();
  }

  clearNotifications() {
    this.tempNotifications = [];
    this.notifications.next(this.tempNotifications);
  }

  publishNotifications() {
    this.tempNotifications = this.tempNotifications.map(v => {
      v.createdAt = new Date(v.createdAt).getTime();
      return v;
    });
    this.sort();
    this.notifications.next(this.tempNotifications);
  }

  sort() {
    this.tempNotifications = this.tempNotifications.sort((a, b) => {
      return b.createdAt - a.createdAt;
    });
  }
}



