import { Component, OnInit } from "@angular/core";
import { MDBSpinningPreloader } from "../typescripts/pro/preloader/preloader.service";
import { NotificationService } from "./shared/notification/notification.service";
import { AuthService } from "./shared/user/auth.service";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.css"]
})
export class AppComponent implements OnInit {
  constructor(
    private mdbSpinningPreloader: MDBSpinningPreloader,
    private notificationService: NotificationService,
    private authService: AuthService
  ) {}

  ngOnInit() {
    this.notificationService.clearNotifications();
    const id_sub = this.authService.$loggedInUserId.subscribe(data => {
      const userId = data || this.authService.loggedInUserId;
      this.notificationService.getNotifications(userId);
    });
    this.mdbSpinningPreloader.stop();
  }
}



