import { environment } from "./../../../environments/environment";
import { Injectable, NgZone } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { ToastService } from "../../../typescripts/pro/alerts";

import { Role } from "./role";

import { BehaviorSubject, Observable } from "rxjs/Rx";
import { share } from "rxjs/operators";
import { MDBSpinningPreloader } from "typescripts/pro";

@Injectable()
export class RoleService {
  public url = environment.corona.ROLE_BASE_URL;
  public roleUrl = this.url + "/usergroup";

  public roles: BehaviorSubject<Array<Role>>;
  public tempRoles: Array<Role>;
  public searchTerm: BehaviorSubject<String>;
  public searchQuery: string;
  public fetchDone: BehaviorSubject<boolean>;

  constructor(
    private http: HttpClient,
    private ngZone: NgZone,
    private mdbSpinningPreloader: MDBSpinningPreloader,
    private toastrService: ToastService
  ) {
    this.tempRoles = [];
    this.roles = new BehaviorSubject([]);
    this.searchTerm = new BehaviorSubject("");
    this.fetchDone = new BehaviorSubject(false);

    this.searchTerm.subscribe(data => {
      this.searchQuery = data.toLowerCase();
      this.publishRoleUpdates();
    });

    this.getRoles().subscribe();
  }

  getRoles() {
    return new Observable(observer => {
      this.fetchDone.next(false);
      this.ngZone.run(() => {
        this.http.get(this.url + "/usergroups").subscribe(data => {
          const roles = data;

          if (roles["userGroups"]) {
            this.tempRoles = roles["userGroups"].map(v => {
              return Object.assign(new Role(), v);
            });
          }

          this.publishRoles();
          this.fetchDone.next(true);
        });
      });
    }).share();
  }

  publishRoles() {
    this.sort();
    this.roles.next(this.tempRoles);
  }

  publishRoleUpdates() {
    this.sort();
    this.roles.next(
      this.tempRoles.filter(v => {
        return v.name.toLowerCase().indexOf(this.searchQuery) >= 0;
      })
    );
  }

  sort() {
    this.tempRoles = this.tempRoles.sort((a, b) => {
      if (a.name < b.name) {
        return -1;
      } else if (a.name > b.name) {
        return 1;
      } else {
        return 0;
      }
    });
  }

  createOrEditRole(role: Role) {
    if (role.userGroupId.length > 0) {
      this.ngZone.run(() => {
        this.http
          .put(this.roleUrl + "/" + role.userGroupId, role, {
            observe: "response"
          })
          .subscribe(
            data => {
              if (data.status === 200) {
                this.getRoles().subscribe();
                this.showSuccess(data.body["message"]);
              }
            },
            error => {
              this.showError(error.error["error"]);
            }
          );
      });
    } else {
      delete role.userGroupId;
      this.ngZone.run(() => {
        this.http.post(this.roleUrl, role, { observe: "response" }).subscribe(
          data => {
            if (data.status === 200) {
              this.getRoles().subscribe();
              this.showSuccess(data.body["message"]);
            }
          },
          error => {
            this.showError(error.error["error"]);
          }
        );
      });
    }
  }

  deleteRole(role: Role) {
    this.tempRoles = this.tempRoles.filter(v => {
      return v.userGroupId !== role.userGroupId;
    });

    this.publishRoleUpdates();
    return this.http.delete(this.roleUrl + "/" + role.userGroupId, { observe: "response" }).subscribe(
      data => {
        if (data.status !== 200) {
          this.tempRoles.push(role);
          this.publishRoleUpdates();
          this.showError(data.body["message"]);
        }
        this.showSuccess(data.body["message"]);
      },
      error => {
        this.tempRoles.push(role);
        this.publishRoleUpdates();
        this.showError(error.error["error"]);
      }
    );
  }
  showSuccess(message) {
    this.toastrService.success(message);
  }

  showError(message) {
    this.toastrService.error(message);
  }
}



