export class User {
  public firstName: string;
  public lastName: string;
  public profilePicture: string;
  public userId: string;
  public isAdUser: Boolean;
  public email: string;
  public mobileNumber: string;
  public teamIds: Array<string>;
  public userGroupId: string;
  public isBusy: Boolean;
  public country: Array<Object>;
  public countryIsoCode: string;
  public languageCulture: string;
  public location: any;

  constructor() {
    this.firstName = "";
    this.lastName = "";
    this.email = "";
    this.profilePicture = "url";
    this.isAdUser = false;
    this.isBusy = false;
    this.mobileNumber = "";
    this.userId = "";
    this.teamIds = [];
    this.userGroupId = "";
    this.country = [];
    this.countryIsoCode = "";
  }

  setCurrentPosition() {
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition(position => {
        this.location = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
      });
    }
  }

  public getFullName() {
    return this.firstName + " " + this.lastName;
  }
}



