export class Role {
  name: string;
  permission: Array<string>;
  userId: string;
  userGroupId: string;
  members: Array<string>;

  constructor() {
    this.name = "";
    this.permission = [];
    this.userId = "";
    this.userGroupId = "";
    this.members = [];
  }
}



