import { Injectable } from "@angular/core";

@Injectable()
export class PermissionService {
  getPermissions() {
    return [
      { value: "corona_mobile_user", label: "Corona Mobile User" },
      { value: "corona_web_user", label: "Corona Web User" },
      { value: "corona_single_assign", label: "Corona Single Assign" },
      { value: "corona_multiple_assign", label: "Corona Multiple Assign" },
      { value: "corona_manage_workforce", label: "Corona Manage Workforce" },
      { value: "corona_web_task_mgt", label: "Corona Web Task Management" },
      { value: "corona_stock_movement", label: "Corona Stock Movement" },
      { value: "corona_stock_management", label: "Corona Stock Management" },
      { value: "corona_decoder_activation", label: "Corona Decoder Activation" },
      { value: "corona_subscription_payment", label: "Corona Subscription And Payment" }
    ];
  }

  constructor() {}
}



