import { environment } from "./../../../environments/environment";
import { Injectable, NgZone } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { ToastService } from "../../../typescripts/pro/alerts";

import { BehaviorSubject, Observable } from "rxjs/Rx";
import { share } from "rxjs/operators";

import { Region } from "./region";
import { MDBSpinningPreloader } from "typescripts/pro";

@Injectable()
export class RegionService {
  public regionbaseUrl = environment.corona.REGION_BASE_URL;
  public regions: BehaviorSubject<Array<any>>;
  public tempRegions: Array<Region>;

  public countryRegions: BehaviorSubject<Array<any>>;
  public tempCountryRegions: Array<any>;

  public states: BehaviorSubject<Array<any>>;
  public tempStates: Array<any>;

  public searchTerm: BehaviorSubject<string>;
  public searchQuery: string;
  public fetchDone: BehaviorSubject<boolean>;

  constructor(
    private http: HttpClient,
    private ngZone: NgZone,
    private mdbSpinningPreloader: MDBSpinningPreloader,
    private toastrService: ToastService
  ) {
    this.regions = new BehaviorSubject([]);
    this.countryRegions = new BehaviorSubject([]);
    this.states = new BehaviorSubject([]);
    this.searchTerm = new BehaviorSubject("");
    this.fetchDone = new BehaviorSubject(false);

    this.tempRegions = [];
    this.tempStates = [];

    this.getRegions().subscribe();

    this.searchTerm.subscribe(data => {
      this.searchQuery = data.toLowerCase();
      this.publishRegionUpdates();
    });
  }

  getRegions() {
    return new Observable(observer => {
      this.fetchDone.next(false);
      this.ngZone.run(() => {
        this.http.get(this.regionbaseUrl + "/regions").subscribe(data => {
          let regions = data["regions"];
          regions = regions.map(v => {
            return Object.assign(new Region(), v);
          });

          this.tempRegions = regions;
          this.publishRegions();
          this.fetchDone.next(true);
        });
      });
    }).share();
  }

  getRegionStates(regionId) {
    this.tempStates = this.tempRegions.filter(data => {
      return data.regionId === regionId;
    })[0].states;

    this.tempStates = this.tempStates.map(data => {
      return { value: data.id, label: data.name };
    });

    this.states.next(this.tempStates);
  }

  getCountryRegions(countryName) {
    this.tempCountryRegions = this.tempRegions
      .filter(elem => {
        return elem.country.toLowerCase() === countryName.toLowerCase();
      })
      .map(v => {
        return { value: v.regionId, label: v.regionName };
      });
    this.countryRegions.next(this.tempCountryRegions);
  }

  publishRegions() {
    this.sortRegions();
    this.regions.next(this.tempRegions);
  }

  publishRegionUpdates() {
    this.sortRegions();
    this.regions.next(
      this.tempRegions.filter(v => {
        return (
          v.regionName.toLowerCase().indexOf(this.searchQuery) >= 0 ||
          v.country.toLowerCase().indexOf(this.searchQuery) >= 0
        );
      })
    );
  }

  sortRegions() {
    this.tempRegions = this.tempRegions.sort((a, b) => {
      if (a.regionName < b.regionName) {
        return -1;
      } else if (a.regionName > b.regionName) {
        return 1;
      } else {
        return 0;
      }
    });
  }

  createOrUpdate(region: Region) {
    if (region.regionId.length <= 0) {
      delete region.regionId;
      return this.http.post(this.regionbaseUrl + "/region", region, { observe: "response" }).subscribe(
        data => {
          if (data.status === 200) {
            this.getRegions().subscribe();
            this.showSuccess(data.body["message"]);
          }
        },
        err => {
          this.showError(err.error["error"]);
        }
      );
    } else {
      return this.http
        .patch(this.regionbaseUrl + "/region/" + region.regionId, region, {
          observe: "response"
        })
        .subscribe(
          data => {
            if (data.status === 200) {
              this.getRegions().subscribe();
              this.showSuccess(data.body["message"]);
            }
          },
          err => {
            this.showError(err.error["error"]);
          }
        );
    }
  }

  getOneRegion(id) {
    return this.http.get(this.regionbaseUrl + "/region/" + id);
  }

  deleteRegion(region: Region) {
    this.tempRegions = this.tempRegions.filter(v => {
      return v.regionId !== region.regionId;
    });

    this.publishRegionUpdates();

    return this.http
      .delete(this.regionbaseUrl + "/region/" + region.regionId, {
        observe: "response"
      })
      .subscribe(
        data => {
          if (data.status !== 200) {
            this.tempRegions.push(region);
            this.publishRegionUpdates();
            this.showError(data["error"]);
          }
          this.showSuccess(data.body["message"]);
        },
        error => {
          this.tempRegions.push(region);
          this.publishRegionUpdates();
          this.showError(error.error["error"]);
        }
      );
  }
  showSuccess(message) {
    this.toastrService.success(message);
  }

  showError(message) {
    this.toastrService.error(message);
  }
}



