import { HttpClient } from "@angular/common/http";
import { Injectable, NgZone } from "@angular/core";

import { BehaviorSubject, Observable } from "rxjs/Rx";
import { share, switchMap, concatMap } from "rxjs/operators";

import { Task } from "./task";
import { TaskService } from "./task.service";
import { TeamStack } from "./../team/team.stack";

@Injectable()
export class ArchivedTaskService {
  public tasks: BehaviorSubject<Array<Task>>;
  public userTasks: BehaviorSubject<Array<Task>>;
  public tempTasks: Array<Task>;

  public downlineTasks: BehaviorSubject<Array<Task>>;
  public tempDownlineTasks: Array<Task>;

  public searchTerm: BehaviorSubject<String>;
  public searchQuery: string;
  public fetchDone: BehaviorSubject<boolean>;

  constructor(
    private http: HttpClient,
    private ngZone: NgZone,
    private taskService: TaskService,
    private teamStack: TeamStack
  ) {
    this.tempTasks = [];
    this.tasks = new BehaviorSubject([]);
    this.userTasks = new BehaviorSubject([]);

    this.tempDownlineTasks = [];
    this.downlineTasks = new BehaviorSubject([]);
    this.fetchDone = new BehaviorSubject(false);

    this.searchTerm = new BehaviorSubject("");

    this.searchTerm.subscribe(data => {
      this.searchQuery = data.toLowerCase();
      this.publishTaskUpdates();
    });

    this.getDownlineTasks().subscribe();
  }

  getDownlineTasks() {
    const request = {
      data: {
        status: ["Done"]
      }
    };
    return this.teamStack.leadTeams
      .switchMap(arr => {
        this.fetchDone.next(false);
        const teamIds = arr.map(item => item.teamId).join(",");
        if (teamIds.length) {
          return this.http
            .post(this.taskService.baseUrl + "/tasks?teamId=" + teamIds, request)
            .concatMap(data => {
              const tasks = data["data"].map(x => {
                return Object.assign(new Task(), x);
              });

              this.tempDownlineTasks = tasks;
              this.downlineTasks.next(this.tempDownlineTasks);
              return tasks;
            })
            .share()
            .finally(() => {
              this.fetchDone.next(true);
            });
        } else {
          return this.downlineTasks;
        }
      })
      .share();
  }

  getOneTask(id) {
    return this.taskService.getOneTask(id);
  }

  getUserTasks(userId) {
    const request = {
      data: {
        status: ["Done"]
      }
    };
    return this.http
      .post(this.taskService.baseUrl + "/tasks?assignee=" + userId, request)
      .concatMap(data => {
        const tasks = data["data"].map(x => {
          return Object.assign(new Task(), x);
        });
        this.userTasks.next(tasks);
        return tasks;
      })
      .share();
  }

  publishTaskUpdates() {
    this.downlineTasks.next(
      this.tempDownlineTasks.filter(v => {
        return v.title.toLowerCase().indexOf(this.searchQuery) >= 0;
      })
    );
  }
}



