import { environment } from "./../../../environments/environment";
import { Injectable, NgZone } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { ToastService } from "../../../typescripts/pro/alerts";
import { AuthService } from "../user/auth.service";

import { BehaviorSubject, Observable } from "rxjs/Rx";
import { share } from "rxjs/operators";

import { Team } from "./team";
import { TeamStack } from "./team.stack";

@Injectable()
export class TeamService {
  public baseUrl = environment.corona.TEAM_BASE_URL;
  public teamByAreaUrl = environment.corona.TEAM_BASE_URL + "/teams?areaOfOperation=";
  public teamByLeadUrl = environment.corona.TEAM_BASE_URL + "/teams?teamLead=";
  public teams: BehaviorSubject<Array<Team>>;
  public tempTeams: Array<Team>;
  public teamsByArea: BehaviorSubject<Array<{ value: string; label: string }>>;
  public tempTeamsByArea: Array<Team>;
  public teamMembers: BehaviorSubject<Array<string>>;
  public loggedInUserDownlines: BehaviorSubject<Array<string>>;
  public loggedInUserTeams: BehaviorSubject<Array<Team>>;
  public searchTerm: BehaviorSubject<String>;
  public searchQuery: string;
  public areaName: string;
  public teamNames: BehaviorSubject<Array<string>>;
  public fetchDone: BehaviorSubject<boolean>;

  constructor(
    private http: HttpClient,
    private ngZone: NgZone,
    private toastrService: ToastService,
    private authService: AuthService,
    private teamStack: TeamStack
  ) {
    this.teams = new BehaviorSubject([]);
    this.teamsByArea = new BehaviorSubject([]);
    this.teamMembers = new BehaviorSubject([]);

    this.loggedInUserDownlines = new BehaviorSubject([]);
    this.loggedInUserTeams = new BehaviorSubject([]);
    this.fetchDone = new BehaviorSubject(false);

    this.teamNames = new BehaviorSubject([]);

    this.tempTeams = [];
    this.tempTeamsByArea = [];

    this.getTeams().subscribe();
    this.getLoggedInUserDownlines(this.authService.getUserId()).subscribe();

    this.searchTerm = new BehaviorSubject("");

    this.searchTerm.subscribe(data => {
      this.searchQuery = data.toLowerCase();
      this.publishTeams(this.teams);
    });
  }

  getTeams() {
    return new Observable(observable => {
      this.fetchDone.next(false);
      this.ngZone.run(() => {
        this.http.get(this.baseUrl + "/teams").subscribe(data => {
          const teams = data["data"];
          this.tempTeams = teams;
          this.publishTeams(this.teams);
          this.fetchDone.next(true);
        });
      });
    }).share();
  }

  getMultipleTeams(teamIds) {
    return new Observable(observable => {
      this.fetchDone.next(false);
      this.http.get(this.baseUrl + "/team/" + teamIds).subscribe(data => {
        const tempTeamNames = [];
        if (data["teams"]) {
          const teams = data["teams"];
          for (const key of Object.keys(teams)) {
            tempTeamNames.push(teams[key].teamName);
          }
        }
        this.teamNames.next(tempTeamNames);
        this.fetchDone.next(true);
      });
    }).share();
  }

  publishTeams(teams) {
    this.tempTeams = this.tempTeams.map(v => {
      const t = Object.assign(new Team(), v);
      return t;
    });

    this.tempTeams = this.tempTeams.sort((a, b) => {
      if (a.teamName < b.teamName) {
        return -1;
      } else if (a.teamName > b.teamName) {
        return 1;
      } else {
        return 0;
      }
    });

    teams.next(
      this.tempTeams.filter(v => {
        return (
          v.teamName.toLowerCase().indexOf(this.searchQuery) >= 0 ||
          v.country.toLowerCase().indexOf(this.searchQuery) >= 0
        );
      })
    );
  }

  getTeamsByArea(areaName) {
    return new Observable(observable => {
      this.ngZone.run(() => {
        this.http.get(this.teamByAreaUrl + areaName).subscribe(data => {
          const areaTeams = data["data"];
          if (areaTeams) {
            this.teamsByArea.next(
              areaTeams.map(team => {
                return { value: team.teamId, label: team.teamName };
              })
            );
            const members = [];
            areaTeams.forEach(team => {
              team.teamMembers.forEach(member => {
                if (member.location) {
                  members.push(member);
                }
              });
            });
            this.teamMembers.next(members);
          }
        });
      });
    }).share();
  }

  getTeamsByLead(teamLeadId, fetchDone) {
    return new Observable(observable => {
      this.ngZone.run(() => {
        if (!teamLeadId) {
          return;
        }

        this.http.get(this.teamByLeadUrl + teamLeadId).subscribe(data => {
          const leadTeams = data["data"];
          if (!leadTeams.length) {
            fetchDone.next(true);
            return this.showError("User has no downlines");
          }
          const members = new Set();
          leadTeams.forEach(team => {
            team.teamMembers.forEach(member => {
              members.add(member);
            });
          });

          this.teamStack.push({
            leadTeams: leadTeams,
            teamMembers: members
          });
        });
      });
    }).share();
  }

  getLoggedInUserDownlines(id) {
    return new Observable(observable => {
      this.ngZone.run(() => {
        this.http.get(this.teamByLeadUrl + id).subscribe(data => {
          let userTeams = data["data"];
          userTeams = userTeams.filter(team => !team.deletedAt); // filter inactive teams
          this.loggedInUserTeams.next(userTeams);
          const members = new Set();
          userTeams.forEach(team => {
            team.teamMembers.forEach(member => {
              members.add(member);
            });
          });

          const users = Array.from(members);
          if (users.indexOf(id) === -1) {
            users.push(id); // add teamLead to team if not listed in team members, so he can assign tasks to himself also
          }
          this.loggedInUserDownlines.next(users);
        });
      });
    }).share();
  }

  createOrUpdateTeam(team: Team) {
    if (!team.teamId.length) {
      delete team.teamId;
      return this.http.post(this.baseUrl + "/team", team, { observe: "response" }).subscribe(
        data => {
          if (data.status === 200) {
            this.getTeams().subscribe();
            this.showSuccess(data.body["message"]);
          }
        },
        err => {
          this.showError(err.error["error"]);
        }
      );
    } else {
      return this.http
        .patch(this.baseUrl + "/team/" + team.teamId, team, {
          observe: "response"
        })
        .subscribe(
          data => {
            if (data.status === 200) {
              this.getTeams().subscribe();
              this.showSuccess(data.body["message"]);
            }
          },
          err => {
            this.showError(err.error["error"]);
          }
        );
    }
  }

  deleteTeam(team: Team) {
    this.tempTeams = this.tempTeams.filter(v => {
      return v.teamId !== team.teamId;
    });
    this.publishTeams(this.teams);

    this.http.delete(this.baseUrl + "/team/" + team.teamId, { observe: "response" }).subscribe(
      data => {
        if (data.status !== 200) {
          this.tempTeams.push(team);
          this.publishTeams(this.teams);
          this.showError(data.body["message"]);
        }
        this.showSuccess(data.body["message"]);
      },
      error => {
        this.tempTeams.push(team);
        this.publishTeams(this.teams);
        this.showError(error.error["error"]);
      }
    );
  }

  updateTeamStatus(team: Team) {
    this.http
      .patch(
        this.baseUrl + "/team/" + team.teamId + "/status",
        {
          deletedAt: team.deletedAt
        },
        {
          observe: "response"
        }
      )
      .subscribe(
        data => {
          if (data.status !== 200) {
            this.getTeams().subscribe();
            this.showError(data.body["message"]);
          }
          this.getTeams().subscribe();
          this.showSuccess(data.body["message"]);
        },
        error => {
          this.showError(error.error["error"]);
        }
      );
  }

  showSuccess(message) {
    this.toastrService.success(message);
  }

  showError(message) {
    this.toastrService.error(message);
  }
}



