export class Template {
  assignmentMode: Array<string>;
  currency: string;
  isRatingApplicable: boolean;
  name: string;
  price: number;
  priorTemplate: Array<any>;
  teams: Array<string>;
  template: Array<any>;
  templateId: string;
  userGroup: Array<string>;
  userId: string;

  constructor() {
    this.assignmentMode = ["Manual Assignment"];
    this.currency = "NGN";
    this.isRatingApplicable = false;
    this.name = "";
    this.price = null;
    this.teams = [];
    this.template = [];
    this.priorTemplate = [];
    this.templateId = "";
    this.userGroup = [];
    this.userId = "";
  }

  setTemplate(template: Array<TemplateField>) {
    this.template = this.preProcessTemplateField(template);
  }

  setPriorTemplate(template: Array<TemplateField>) {
    this.priorTemplate = this.preProcessTemplateField(template);
  }

  preProcessTemplateField(template: Array<TemplateField>) {
    return template.map(field => {
      const validationKey = field.validation;
      const validation = {};

      if (validationKey.length) {
        validation[validationKey] = true;
      }

      return {
        fieldName: field.fieldName,
        type: field.type,
        order: field.order,
        validation: validation,
        dropDownValues: field.dropDownValues
          .split(",")
          .filter(v => {
            return v.length > 0;
          })
          .map(v => {
            v = v.trim();
            const obj = {};
            obj[v] = v;
            return obj;
          })
      };
    });
  }

  getTemplate() {
    return this.convertTemplateFields(this.template);
  }

  getPriorTemplate() {
    return this.convertTemplateFields(this.priorTemplate);
  }

  convertTemplateFields(template) {
    return template.map(v => {
      const templateField = new TemplateField();

      templateField.fieldName = v["fieldName"];
      templateField.type = v["type"];
      templateField.order = v["order"];

      if (v["validation"]) {
        templateField.validation = Object.keys(v["validation"]).join("");
      }

      if (v["dropDownValues"]) {
        templateField.dropDownValues = v["dropDownValues"]
          .map(p => {
            return Object.keys(p).join(", ");
          })
          .join(", ");
      }

      return templateField;
    });
  }
}

export class TemplateField {
  public fieldName: string;
  public type: string;
  public order: number;
  public validation: string;
  public dropDownValues: any;

  constructor() {
    this.fieldName = "";
    this.type = "text";
    this.order = 1;
    this.validation = "none";
    this.dropDownValues = "";
  }
}



