import { LogoutComponent } from "./logout/logout.component";
import { ModuleWithProviders } from "@angular/core";
import { Routes, ActivatedRoute, RouterModule } from "@angular/router";

import { AuthGuard } from "./shared/user/auth-guard.service";

const CoronaRoutes: Routes = [
  { path: "", redirectTo: "/login", pathMatch: "full" },
  {
    path: "login",
    loadChildren: "app/login/login.module#LoginModule"
  },
  { path: "logout", loadChildren: "app/logout/logout.module#LogoutModule" },
  {
    path: "feedback",
    loadChildren: "app/feedback/feedback.module#FeedbackModule"
  },
  {
    path: "home",
    canActivate: [AuthGuard],
    loadChildren: "app/base/base.module#BaseModule"
  },
  { path: "404", loadChildren: "app/not-found/not-found.module#NotFoundModule" },
  { path: "**", redirectTo: "/404" }
];

export const CoronaRouting: ModuleWithProviders = RouterModule.forRoot(CoronaRoutes);



