import { User } from "./../user/user";
import { Task } from "./../task/task";
import { Injectable, NgZone } from "@angular/core";
import { environment } from "../../../environments/environment";
import { HttpClient } from "@angular/common/http";
import { AuthService } from "../user/auth.service";
import { TaskService } from "../task/task.service";
import { UserService } from "../user/user.service";

import { BehaviorSubject, Observable } from "rxjs/Rx";
import { share } from "rxjs/operators";

import { Rating } from "./rating";
import { MDBSpinningPreloader } from "typescripts/pro";

@Injectable()
export class RatingService {
  public ratingUrl = environment.corona.RATING_BASE_URL;
  public taskUrl = environment.corona.TASK_BASE_URL;
  public ratings: BehaviorSubject<Array<Rating>>;
  public tempRatings: Array<Rating>;
  public tasks: BehaviorSubject<Object>;
  public fetchTasks = "";
  public fetchDone: BehaviorSubject<boolean>;

  constructor(
    private http: HttpClient,
    private ngZone: NgZone,
    private authService: AuthService,
    private taskService: TaskService,
    private mdbSpinningPreloader: MDBSpinningPreloader,
    private userService: UserService
  ) {
    this.ratings = new BehaviorSubject([]);
    this.tasks = new BehaviorSubject({});
    this.tempRatings = [];
    this.fetchDone = new BehaviorSubject(false);

    this.getRatings().subscribe();
  }

  getRatings() {
    return new Observable(observer => {
      this.fetchDone.next(false);
      this.ngZone.run(() => {
        this.http.get(this.ratingUrl + "/ratings").subscribe(resp => {
          let ratings = resp["data"];

          ratings = ratings.map(v => {
            this.fetchTasks = this.fetchTasks + v.taskId + ",";
            return Object.assign(new Rating(), v);
          });

          this.tempRatings = ratings;
          this.publishRatings();
          this.fetchDone.next(true);
        });
      });
    }).share();
  }

  sortRating() {
    this.tempRatings = this.tempRatings.sort((a, b) => {
      if (a.rating < b.rating) {
        return -1;
      } else if (a.rating > b.rating) {
        return 1;
      } else {
        return 0;
      }
    });

    this.ratings.next(this.tempRatings);
  }

  publishRatings() {
    this.sortRating();
    this.ratings.next(this.tempRatings);
    this.getTasks();
  }

  getTasks() {
    if (this.fetchTasks) {
      this.http.get(this.taskUrl + "/task/" + this.fetchTasks.slice(0, this.fetchTasks.length - 1)).subscribe(res => {
        this.tasks.next(res["data"]);
      });
    } else {
      this.tasks.next({});
    }
  }
}



