import { Template } from "../template/template";

export class Task {
  taskId: string;
  title: string;
  deviceOrAgentNumber: string;
  startDateTime: number;
  templateFieldsValues: TaskTemplateFieldsValues;
  template: Template;
  location: any;
  endDateTime: number;
  frequency: string;
  status: string;
  description: string;
  assigner: string;
  reviewer: string;
  assignee: string;
  teamId: string;
  cost: TaskCost;
  comments: Array<TaskComment>;
  createdAt: any;
  overdue?: boolean;

  constructor() {
    this.taskId = "";
    this.title = "";
    this.createdAt = 0;
    this.startDateTime = 0;
    this.endDateTime = 0;
    this.template = new Template();
    this.templateFieldsValues = new TaskTemplateFieldsValues();
    this.frequency = "";
    this.teamId = "";
    this.status = "";
    this.assignee = "";
    this.assigner = "";
    this.reviewer = "";
    this.cost = new TaskCost();
    this.location = {};
    this.comments = [];
  }
}

export class TaskComment {
  userId: string;
  comment: string;
}

export class TaskCost {}

export class TaskTemplateFieldsValues {
  priorTemplate: any;
  template: any;
  constructor() {
    this.priorTemplate = {};
    this.template = {};
  }
}



