import { environment } from "./../../../environments/environment";
import { User } from "./user";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs/Observable";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { HttpClient } from "@angular/common/http";
import { ToastService } from "../../../typescripts/pro/alerts";
import "rxjs/add/operator/map";
import { Router } from "@angular/router";
import { MDBSpinningPreloader } from "../../../typescripts/pro/preloader/preloader.service";
import { NotificationService } from "./../notification/notification.service";

@Injectable()
export class AuthService {
  public token: string;
  public loggedInUserId: string;
  public url = environment.corona.USER_BASE_URL;
  public $cpType: BehaviorSubject<string>;
  public $cpMessage: BehaviorSubject<string>;

  public $loggedInUserId: BehaviorSubject<string>;

  constructor(
    private http: HttpClient,
    private router: Router,
    private mdbSpinningPreloader: MDBSpinningPreloader,
    private toastrService: ToastService,
    private notificationService: NotificationService
  ) {
    const currentUser = JSON.parse(localStorage.getItem("currentUser"));
    this.token = currentUser && currentUser.token;
    this.loggedInUserId = currentUser && currentUser.userId;

    this.$loggedInUserId = new BehaviorSubject("");
    this.$cpMessage = new BehaviorSubject("");
    this.$cpType = new BehaviorSubject("");
  }

  getUser() {
    if (localStorage.getItem("currentUser")) {
      return JSON.parse(localStorage.getItem("currentUser"));
    }
  }

  getUserId(): string {
    if (localStorage.getItem("currentUser")) {
      return JSON.parse(localStorage.getItem("currentUser")).userId;
    }
  }

  checkExpiry() {
    const expiry = this.getUser() && this.getUser().expiry;
    if (expiry < Date.now()) {
      return false;
    }
    return true;
  }

  resetPasswordlinkCreate(email: string): Observable<boolean> {
    return this.http.post(this.url + "/reset-password", JSON.stringify({ email })).map(response => {
      const msg = response;
      if (msg) {
        // return true to indicate successful login
        return true;
      } else {
        // return false to indicate failed login
        return false;
      }
    });
  }

  changePassword(password: string, newPassword: string, userName: string = this.getUser().stsUsername) {
    this.$cpType.next("none");
    this.$cpMessage.next("");

    return this.http
      .post(this.url + "/change-password", JSON.stringify({ userName, password, newPassword }), {
        observe: "response"
      })
      .subscribe(
        result => {
          if (result.status === 200) {
            this.$cpType.next("success");
            this.$cpMessage.next(result.body["message"]);

            setTimeout(() => {
              this.router.navigate(["/login"]);
            }, 1000);
            return true;
          } else {
            this.$cpType.next("error");
            this.$cpMessage.next(result.body["error"]);

            return false;
          }
        },
        error => {
          this.$cpType.next("error");
          this.$cpMessage.next(error.error["error"]);

          return false;
        }
      );
  }

  login(userName: string, password: string): Observable<boolean> {
    this.notificationService.clearNotifications();
    return this.http.post(this.url + "/login", JSON.stringify({ userName, password })).map((response: Response) => {
      const user = response["data"].user;
      const token = response["data"] && response["data"].token;
      const expiry = response["data"] && response["data"].expiry * 1000;
      const userId = user && user.userId;
      if (!user.permissions.corona_web_user) {
        return false;
      }
      if (user && token) {
        //   store userName and jwt token in local storage to keep user logged in between page refreshes
        this.token = token;
        this.loggedInUserId = userId;
        this.$loggedInUserId.next(this.loggedInUserId);
        localStorage.setItem("currentUser", JSON.stringify({ ...user, token, expiry }));
        return { ...user, value: true };
      } else {
        return false;
      }
    });
  }

  checkAuth() {
    const expiry = this.getUser() && this.getUser().expiry;
    if (expiry) {
      if (expiry < Date.now()) {
        this.logout();
      } else {
        this.router.navigate(["/home"]);
      }
    } else {
      this.router.navigate(["/login"]);
    }
  }

  logout(): void {
    // clear token and logged in user id, remove user from local storage to log user outreturn this.http
    this.notificationService.clearNotifications();
    const userId = this.getUserId();
    this.mdbSpinningPreloader.start();
    this.http.post(this.url + `/user/${userId}/logout`, JSON.stringify({ userId })).subscribe(d => {
      this.$loggedInUserId.next("");
      this.token = null;
      this.loggedInUserId = null;
      localStorage.removeItem("currentUser");
      this.router.navigate(["/login"]);
    });
  }

  showSuccess(message) {
    this.toastrService.success(message);
  }

  showError(message) {
    this.toastrService.error(message);
  }
}



