import { Pipe, PipeTransform } from "@angular/core";
import { BasicFunctions } from "../shared/utility/basic-functions";

@Pipe({
  name: "sortNotifications"
})
export class SortNotifications implements PipeTransform {
  constructor(public basicFunctions: BasicFunctions) {}
  transform(value: Array<any>, args: string): any {
    const now = this.basicFunctions.getDateFormat(new Date(Date.now()), "-");
    if (args === "today") {
      return value.filter(note => this.basicFunctions.getDateFormat(new Date(note.createdAt), "-") === now);
    } else if (args === "earlier") {
      return value.filter(note => this.basicFunctions.getDateFormat(new Date(note.createdAt), "-") !== now);
    } else if (args === "todayCount") {
      return value.filter(note => this.basicFunctions.getDateFormat(new Date(note.createdAt), "-") === now).length;
    } else if (args === "earlierCount") {
      return value.filter(note => this.basicFunctions.getDateFormat(new Date(note.createdAt), "-") !== now).length;
    }
  }
}



