import { environment } from "./../../../environments/environment";
import { Injectable, NgZone } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { ToastService } from "../../../typescripts/pro/alerts";
import { MDBSpinningPreloader } from "../../../typescripts/pro/preloader/preloader.service";

import { BehaviorSubject, Observable } from "rxjs/Rx";
import { map } from "rxjs/operators";

import { AuthService } from "../user/auth.service";
import { User } from "./user";
import { RoleService } from "../role/role.service";
import { TeamService } from "../team/team.service";
import { RegionService } from "../region/region.service";
import { AreaService } from "../area/area.service";

@Injectable()
export class UserService {
  public url = environment.corona.USER_BASE_URL;
  public userBucket: Array<User>;
  public users: BehaviorSubject<Array<User>>;
  public teamUsers: BehaviorSubject<Array<User>>;
  public searchTerm: BehaviorSubject<string>;
  public searchQuery: string;

  public teams;
  public roles: BehaviorSubject<Array<any>>;
  public ImageUrl: BehaviorSubject<any>;
  public fetchDone: BehaviorSubject<boolean>;
  public fetchDone2: BehaviorSubject<boolean>;

  constructor(
    private http: HttpClient,
    private ngZone: NgZone,
    private authService: AuthService,
    private mdbSpinningPreloader: MDBSpinningPreloader,
    private toastrService: ToastService,
    private roleService: RoleService,
    private teamService: TeamService,
    private regionService: RegionService,
    private areaService: AreaService
  ) {
    this.userBucket = [];
    this.users = new BehaviorSubject([]);
    this.teamUsers = new BehaviorSubject([]);
    this.ImageUrl = new BehaviorSubject("");
    this.teams = [];
    this.roles = new BehaviorSubject([]);
    this.searchTerm = new BehaviorSubject("");
    this.fetchDone = new BehaviorSubject(false);
    this.fetchDone2 = new BehaviorSubject(false);

    this.searchTerm.subscribe(data => {
      this.searchQuery = data.toLowerCase();
      this.publishUserUpdates();
    });

    this.roleService.roles.subscribe(roles => {
      this.roles.next(
        roles.map(role => {
          return { value: role.userGroupId, label: role.name };
        })
      );
    });

    this.teamService.teams.subscribe(teams => {
      this.teams = teams;
    });

    this.getUsers().subscribe();
  }

  getUser(id) {
    return this.http.get(this.url + "/user/" + id);
  }

  getUsers() {
    return new Observable(observe => {
      this.fetchDone.next(false);
      this.ngZone.run(() => {
        this.http.get(this.url + "/users").subscribe(res => {
          const result = res;
          if (result["data"]) {
            this.userBucket = result["data"].map(user => {
              user.teamName = this.getTeamNames(user);
              user.roleName = this.getUserGroupName(user);
              user.region = this.getRegionFromTeam(this.getTeam(user));
              user.subRegion = this.getSubRegionFromTeam(this.getTeam(user));
              return Object.assign(new User(), user);
            });
          }
          this.publishUsers();
          this.fetchDone.next(true);
        });
      });
    });
  }

  getMultipleUsers(userIds) {
    this.fetchDone2.next(false);
    return this.http.get(this.url + "/users/" + userIds).subscribe(res => {
      if (res["data"]) {
        const result = res["data"];
        const tempUsers = [];
        for (const key of Object.keys(res["data"])) {
          res["data"][key].teamName = this.getTeamNames(res["data"][key]);
          res["data"][key].roleName = this.getUserGroupName(res["data"][key]);
          res["data"][key].region = this.getRegionFromTeam(this.getTeam(res["data"][key]));
          res["data"][key].subRegion = this.getSubRegionFromTeam(this.getTeam(res["data"][key]));
          tempUsers.push(Object.assign(new User(), res["data"][key]));
        }
        this.userBucket = tempUsers;
      }
      this.sort();
      this.teamUsers.next(this.userBucket);
      this.fetchDone2.next(true);
    });
  }

  getUserNameByUserId(userId) {
    const patt = RegExp("^[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}$");
    const user = this.users.value.filter(elem => {
      return elem.userId === userId;
    });

    if (user.length) {
      return user[0].getFullName();
    }

    if (!userId || patt.test(userId)) {
      return "N/A";
    } else {
      return userId;
    }
  }

  getTeam(user: User) {
    if (user && user.teamIds) {
      const teams = [];
      user.teamIds.map(id => {
        teams.push(
          this.teams.filter(v => {
            return v["teamId"] === id;
          })[0]
        );
      });
      return teams;
    }
  }

  getTeamNames(user: User) {
    if (user && user.teamIds) {
      const teams = user.teamIds
        .map(id => {
          return this.teams
            .filter(v => {
              return v.teamId === id;
            })
            .map(v => {
              return v.teamName;
            })
            .join(", ");
        })
        .filter(team => {
          return team.length > 0;
        })
        .join(", ");
      return teams.length > 0 ? teams : "N/A";
    }
  }

  getUserGroupName(user: User) {
    const group = this.roles.value.filter(v => {
      return v.value === user.userGroupId;
    });
    return group.length ? group[0].label : "N/A";
  }

  getRegionNameByRegionId(regionId) {
    const region = this.regionService.regions.value.filter(regions => {
      return regions.regionId === regionId;
    });

    if (region.length) {
      return region[0].regionName;
    }

    return "N/A";
  }

  getAreaNameByAreaId(areaId) {
    const area = this.areaService.areas.value.filter(areaItem => {
      return areaItem.areaId === areaId;
    });

    if (area.length) {
      return area[0].areaName;
    }
    return "N/A";
  }

  getRegionFromTeam(teams) {
    if (teams) {
      const regions = [];
      for (const team of teams) {
        if (team && team.region && !regions.includes(team.region) && team.region !== "") {
          if (!regions.includes(this.getRegionNameByRegionId(team.region))) {
            regions.push(this.getRegionNameByRegionId(team.region));
          }
        }
      }
      return regions.join(", ");
    }
  }

  getSubRegionFromTeam(teams) {
    if (teams) {
      const subRegions = [];
      for (const team of teams) {
        if (team && team.areaOfOperation && !subRegions.includes(team.areaOfOperation) && team.areaOfOperation !== "") {
          if (!subRegions.includes(this.getAreaNameByAreaId(team.areaOfOperation))) {
            subRegions.push(this.getAreaNameByAreaId(team.areaOfOperation));
          }
        }
      }
      return subRegions.join(", ");
    }
  }

  publishUsers() {
    this.sort();
    this.users.next(this.userBucket);
  }

  publishUserUpdates() {
    this.sort();
    this.teamUsers.next(
      this.userBucket.filter(value => {
        return (
          value.firstName.toLowerCase().indexOf(this.searchQuery) >= 0 ||
          value.lastName.toLowerCase().indexOf(this.searchQuery) >= 0 ||
          value.email.toLowerCase().indexOf(this.searchQuery) >= 0 ||
          value.mobileNumber.toLowerCase().indexOf(this.searchQuery) >= 0
        );
      })
    );
  }

  sort() {
    this.userBucket = this.userBucket.sort((a, b) => {
      if (a.firstName < b.firstName) {
        return -1;
      } else if (a.firstName > b.firstName) {
        return 1;
      } else {
        return 0;
      }
    });
  }

  createOrUpdateUser(user: User) {
    if (user.userId.length > 0) {
      this.http.patch(this.url + "/user/" + user.userId, user).subscribe(
        result => {
          this.getUsers().subscribe();
          this.ImageUrl.next(user.profilePicture);
          this.showSuccess(result["message"]);
        },
        error => {
          this.showError(error.error["error"]);
        }
      );
    } else {
      delete user.userId;
      this.http.put(this.url + "/user", user, { observe: "response" }).subscribe(
        result => {
          if (result.status === 200) {
            this.getUsers().subscribe();
            this.showSuccess(result.body["message"]);
          }
        },
        error => {
          this.showError(error.error["error"]);
          console.log(error);
        }
      );
    }
  }

  updateUser(user: User) {
    this.http.patch(this.url + "/user/" + user.userId, user, { observe: "response" }).subscribe(
      result => {
        if (result.status === 200) {
          this.getUsers().subscribe();
          this.showSuccess(result.body["message"]);
        }
      },
      error => {
        this.showError(error.error["error"]);
      }
    );
  }

  deleteUser(user: User) {
    this.userBucket = this.userBucket.filter(value => {
      return value.userId !== user.userId;
    });
    this.publishUserUpdates();
    this.http.delete(this.url + "/user/" + user.userId, { observe: "response" }).subscribe(
      result => {
        if (result.status !== 200) {
          this.userBucket.push(user);
          this.publishUserUpdates();
          this.showError(result["error"]);
        }
        this.showSuccess(result.body["message"]);
      },
      error => {
        this.userBucket.push(user);
        this.publishUserUpdates();
        this.showError(error.error["error"]);
      }
    );
  }

  showSuccess(message) {
    this.toastrService.success(message);
  }

  showError(message) {
    this.toastrService.error(message);
  }
}



