import { environment } from "./../../../environments/environment";
import { Injectable, NgZone } from "@angular/core";

import { HttpClient } from "@angular/common/http";
import { ToastService } from "../../../typescripts/pro/alerts";

import { BehaviorSubject, Observable } from "rxjs/Rx";
import { share } from "rxjs/operators";

import { Area } from "./area";
import { MDBSpinningPreloader } from "typescripts/pro";

@Injectable()
export class AreaService {
  public baseUrl = environment.corona.AREA_BASE_URL;
  public areas: BehaviorSubject<Array<Area>>;
  public stateAreas: BehaviorSubject<Array<any>>;
  public fetchDone: BehaviorSubject<boolean>;

  public tempAreas: Array<Area>;
  public tempStateAreas: Array<any>;

  public searchTerm: BehaviorSubject<string>;
  public searchQuery: string;
  constructor(
    private http: HttpClient,
    private ngZone: NgZone,
    private mdbSpinningPreloader: MDBSpinningPreloader,
    private toastrService: ToastService
  ) {
    this.areas = new BehaviorSubject([]);
    this.stateAreas = new BehaviorSubject([]);
    this.searchTerm = new BehaviorSubject("");
    this.fetchDone = new BehaviorSubject(false);

    this.getAreas().subscribe();

    this.tempAreas = [];
    this.tempStateAreas = [];

    this.searchTerm.subscribe(data => {
      this.searchQuery = data.toLowerCase();
      this.publishAreasUpdates();
    });
  }

  getAreas() {
    return new Observable(observer => {
      this.fetchDone.next(false);
      this.ngZone.run(() => {
        this.http.get(this.baseUrl + "/areas").subscribe(data => {
          let areas = data["areas"];
          areas = areas.map(v => {
            return Object.assign(new Area(), v);
          });

          this.tempAreas = areas;
          this.publishAreas();
          this.fetchDone.next(true);
        });
      });
    }).share();
  }

  getStateAreas(stateName) {
    this.tempStateAreas = this.tempAreas
      .filter(data => {
        return data.state.toLowerCase() === stateName.toLowerCase();
      })
      .map(data => {
        return { value: data.areaId, label: data.areaName };
      });

    this.stateAreas.next(this.tempStateAreas);
  }

  publishAreas() {
    this.sortAreas();
    this.areas.next(this.tempAreas);
  }

  publishAreasUpdates() {
    this.sortAreas();
    this.areas.next(
      this.tempAreas.filter(v => {
        return (
          v.areaName.toLowerCase().indexOf(this.searchQuery) >= 0 ||
          v.country.toLowerCase().indexOf(this.searchQuery) >= 0
        );
      })
    );
  }

  sortAreas() {
    this.tempAreas = this.tempAreas.sort((a, b) => {
      if (a.areaName < b.areaName) {
        return -1;
      } else if (a.areaName > b.areaName) {
        return 1;
      } else {
        return 0;
      }
    });

    this.areas.next(this.tempAreas);
  }

  createOrUpdate(area: Area) {
    if (area.areaId.length <= 0) {
      delete area.areaId;
      return this.http.post(this.baseUrl + "/area", area, { observe: "response" }).subscribe(
        data => {
          if (data.status === 200) {
            this.getAreas().subscribe();
            this.showSuccess(data.body["message"]);
          }
        },
        err => {
          this.showError(err.error["error"]);
        }
      );
    } else {
      return this.http
        .patch(this.baseUrl + "/area/" + area.areaId, area, {
          observe: "response"
        })
        .subscribe(
          data => {
            if (data.status === 200) {
              this.getAreas().subscribe();
              this.showSuccess(data.body["message"]);
            }
          },
          err => {
            this.showError(err.error["error"]);
          }
        );
    }
  }

  deleteArea(area) {
    this.tempAreas = this.tempAreas.filter(v => {
      return v.areaId !== area.areaId;
    });

    this.publishAreasUpdates();

    return this.http.delete(this.baseUrl + "/area/" + area.areaId, { observe: "response" }).subscribe(
      data => {
        if (data.status !== 200) {
          this.tempAreas.push(area);
          this.publishAreasUpdates();
          this.showError(data["error"]);
        }
        this.showSuccess(data.body["message"]);
      },
      error => {
        this.tempAreas.push(area);
        this.publishAreasUpdates();
        this.showError(error.error["error"]);
      }
    );
  }
  showSuccess(message) {
    this.toastrService.success(message);
  }

  showError(message) {
    this.toastrService.error(message);
  }
}



