import { environment } from "./../../../environments/environment";
import { Injectable, NgZone } from "@angular/core";
import { Observable } from "rxjs/Observable";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import { HttpClient } from "@angular/common/http";
import { ToastService } from "../../../typescripts/pro/alerts";
import "rxjs/add/operator/share";
import "rxjs/add/observable/forkJoin";
import { Task } from "./task";

@Injectable()
export class TaskService {
  public baseUrl = environment.corona.TASK_BASE_URL;
  public geoCodeUrl = environment.corona.googleMap.GEO_CODE_URL;
  public address: string;
  constructor(private http: HttpClient, private ngZone: NgZone, private toastrService: ToastService) { }

  showSuccess(message) {
    this.toastrService.success(message);
  }

  showError(message) {
    this.toastrService.error(message);
  }

  deleteTask(task: Task) {
    return this.http.delete(this.baseUrl + "/task/" + task.taskId, {
      observe: "response"
    });
  }

  updateTask(task: Task, downlineTasksObservable) {
    return this.http
      .patch(this.baseUrl + "/task/" + task.taskId, task, {
        observe: "response"
      })
      .subscribe(
        data => {
          if (data.status === 200) {
            downlineTasksObservable.subscribe();
            this.showSuccess(data.body["message"]);
          }
        },
        err => {
          this.showError(err.error["error"]);
        }
      );
  }

  getOneTask(id) {
    return this.http.get(this.baseUrl + "/task/" + id, { observe: "response" });
  }

  getAddress(lat, lng) {
    return new Observable(observable => {
      this.ngZone.run(() => {
        this.http
          .get(this.geoCodeUrl + lat + "," + lng + "&key=" + environment.corona.googleMap.KEY)
          .subscribe(data => {
            const add = data;
            this.address = add["results"][0].formatted_address;
          });
      });
    }).share();
  }
}



