export class Rating {
  taskId: string;
  rating: number;
  assignee: string;
  createdAt: string;
  url: string;
  radingId: string;
  additionalInfo: string;
  task?: any;
  user?: any;

  constructor() {
    this.taskId = "";
    this.rating = 0;
    this.assignee = "";
    this.createdAt = "";
    this.url = "";
    this.radingId = "";
    this.additionalInfo = "";
  }
}



