import { Injectable } from "@angular/core";
import { MapsAPILoader } from "@agm/core";
import { Observable } from "rxjs/Observable";
import { BehaviorSubject } from "rxjs/BehaviorSubject";

declare var google: any;

@Injectable()
export class MapsService {
  public locate: BehaviorSubject<Array<any>>;
  constructor(private __loader: MapsAPILoader) {
    this.locate = new BehaviorSubject([]);
  }

  getGeocoding(address: string): Observable<any> {
    return Observable.create(observer => {
      try {
        // at this point the variable google may be still undefined (google maps scripts still loading)
        // so load all the scripts, then...
        this.__loader.load().then(() => {
          const geocoder = new google.maps.Geocoder();
          geocoder.geocode({ address }, (results, status) => {
            if (status === google.maps.GeocoderStatus.OK) {
              const loc = []; // no need to define it in outer function now
              loc[0] = results[0].geometry.location.lat();
              loc[1] = results[0].geometry.location.lng();
              loc[2] = results[0].geometry.location;
              this.locate.next(loc);

              observer.complete();
            } else {
              console.error("Error - ", results, " & Status - ", status);
              if (status === google.maps.GeocoderStatus.ZERO_RESULTS) {
                observer.error("Address not found!");
              } else {
                observer.error(status);
              }

              observer.complete();
            }
          });
        });
      } catch (error) {
        observer.error("error getGeocoding" + error);
        observer.complete();
      }
    });
  }
}



