import { Pipe, PipeTransform } from "@angular/core";

import * as moment from "moment";

@Pipe({
  name: "sortTask"
})
export class SortTask implements PipeTransform {
  transform(value: Array<any>, args: string): any {
    if (args === "overdue") {
      return value.filter(function(item) {
        if (item.overdue === true && item.status.toLowerCase() !== "paused") {
          item.templateFieldsValues.priorTemplate.endDateTime = moment(
            item.templateFieldsValues.priorTemplate.endDateTime
          ).format("DD MMM YYYY hh:mm a");
          return item;
        }
      });
    } else if (args === "today") {
      return value.filter(function(item) {
        // tasks allowed are tasks that are not overdue and not anytime, the task can be one-off, weekly, monthly, yearly
        if (item.overdue === false && item.frequency.toLowerCase() !== "anytime") {
          const endTaskTime = moment(item.templateFieldsValues.priorTemplate.endDateTime);
          const startTaskTime = moment(item.templateFieldsValues.priorTemplate.startDateTime);
          const now = moment();
          if (now.isBetween(startTaskTime, endTaskTime)) {
            const timeDiff = moment.duration(endTaskTime.diff(now), "milliseconds");
            item.timeDiff = Math.floor(timeDiff.asHours());
            return item;
          }
        }
      });
    } else if (args === "anytime") {
      // tasks allowed are strictly tasks with anytime frequency
      return value.filter(item => item.frequency.toLowerCase() === "anytime");
    } else if (args === "frequent") {
      // tasks allowed are strictly tasks with other frequency aside anytime and one off
      return value.filter(
        item =>
          item.frequency.toLowerCase() !== "one off" &&
          item.frequency.toLowerCase() !== "anytime" &&
          item.overdue === false &&
          item.status.toLowerCase() !== "paused"
      );
    } else if (args === "weekly") {
      // tasks allowed are strictly tasks with other frequency aside anytime and one off
      return value.filter(item => item.frequency.toLowerCase() === "weekly" && item.overdue === false);
    } else if (args === "monthly") {
      // tasks allowed are strictly monthly tasks
      return value.filter(item => item.frequency.toLowerCase() === "monthly" && item.overdue === false);
    } else if (args === "quarterly") {
      // tasks allowed are strictly weekly tasks
      return value.filter(item => item.frequency.toLowerCase() === "quarterly" && item.overdue === false);
    } else if (args === "others") {
      // tasks allowed are strictly weekly tasks
      return value.filter(function(item) {
        if (item.overdue === false && item.frequency.toLowerCase() !== "anytime") {
          const endTaskTime = moment(item.templateFieldsValues.priorTemplate.endDateTime);
          const startTaskTime = moment(item.templateFieldsValues.priorTemplate.startDateTime);
          const now = moment();
          if (!now.isBetween(startTaskTime, endTaskTime)) {
            const timeDiff = moment.duration(endTaskTime.diff(now), "milliseconds");
            item.timeDiff = Math.floor(timeDiff.asHours());
            item.templateFieldsValues.priorTemplate.endDateTime = moment(
              item.templateFieldsValues.priorTemplate.endDateTime
            ).format("DD MMM YYYY hh:mm a");
            return item;
          }
        }
      });
    }
  }
}



