import { environment } from "./../environments/environment";
import { AgmCoreModule } from "@agm/core";
import { BrowserModule } from "@angular/platform-browser";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { FormsModule } from "@angular/forms";
import { HttpClientModule } from "@angular/common/http";
import { HTTP_INTERCEPTORS } from "@angular/common/http";
import { MDBBootstrapModule } from "../typescripts/free/index";
import { MDBBootstrapModulePro } from "../typescripts/pro/index";
import { MDBSpinningPreloader } from "../typescripts/pro/preloader/preloader.service";
import "aws-sdk/dist/aws-sdk";
import { NgModule, NO_ERRORS_SCHEMA } from "@angular/core";
import { NgPipesModule } from "ngx-pipes";
import { Router, ActivatedRoute, Params } from "@angular/router";
import { ToastModule } from "../typescripts/pro/alerts";

import { ActiveTaskService } from "./shared/task/activetask.service";
import { AreaService } from "./shared/area/area.service";
import { ArchivedTaskService } from "./shared/task/archived-task.service";
import { AuthGuard } from "./shared/user/auth-guard.service";
import { AuthService } from "./shared/user/auth.service";
import { EscalatedTaskService } from "./shared/task/escalated-task.service";
import { InactiveTaskService } from "./shared/task/inactivetask.service";
import { LocationService } from "./shared/location/location.service";
import { MapsService } from "./shared/maps/maps.service";
import { NotificationService } from "./shared/notification/notification.service";
import { PermissionService } from "./shared/role/permission.service";
import { RatingService } from "./shared/rating/rating.service";
import { RegionService } from "./shared/region/region.service";
import { RoleService } from "./shared/role/role.service";
import { SideNavService } from "./shared/sidenav.service";
import { TaskReviewService } from "./shared/task/task-review.service";
import { TaskService } from "./shared/task/task.service";
import { TeamService } from "./shared/team/team.service";
import { TokenService } from "./shared/user/token.service";
import { UserService } from "./shared/user/user.service";
import { StockService } from "./shared/stock/stock.service";
import { MomAndPopsService } from "./shared/stock/mom-and-pops.service";

import { CoronaRouting } from "./app.routing";
import { TeamStack } from "./shared/team/team.stack";
import { TemplateService } from "./shared/template/template.service";
import { TokenInterceptor } from "./shared/user/token.interceptor";

import { AppComponent } from "./app.component";
import { LoggedInNavbarComponent } from "./navbar/logged-in-navbar/logged-in-navbar.component";
import { LoggedOutNavbarComponent } from "./navbar/logged-out-navbar/logged-out-navbar.component";
import { MaterialSpinnerComponent } from "./material-spinner/material-spinner.component";
import { NavbarComponent } from "./navbar/navbar.component";
import { SharedModule } from "app/shared/shared.module";

import { SortTask } from "./profile/profile-task-sort";
import { NotificationComponent } from "./notification/notification.component";
import { SortNotifications } from "./notification/notification-sort";
import { LogoutComponent } from "./logout/logout.component";
import { BasicFunctions } from "./shared/utility/basic-functions";
@NgModule({
  declarations: [AppComponent, NavbarComponent, LoggedInNavbarComponent, LoggedOutNavbarComponent],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    HttpClientModule,
    MDBBootstrapModule.forRoot(),
    MDBBootstrapModulePro.forRoot(),
    ToastModule.forRoot(),
    CoronaRouting,
    SharedModule.forRoot(),
    NgPipesModule,
    AgmCoreModule.forRoot({
      apiKey: environment.corona.googleMap.KEY
    })
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true
    },
    AreaService,
    LocationService,
    RatingService,
    RegionService,
    RoleService,
    SideNavService,
    PermissionService,
    TeamService,
    TeamStack,
    TaskService,
    ActiveTaskService,
    MapsService,
    NotificationService,
    TemplateService,
    UserService,
    StockService,
    MomAndPopsService,
    AuthService,
    AuthGuard,
    MDBSpinningPreloader,
    TokenService,
    InactiveTaskService,
    TaskReviewService,
    EscalatedTaskService,
    ArchivedTaskService,
    BasicFunctions
  ],
  bootstrap: [AppComponent],
  schemas: [NO_ERRORS_SCHEMA]
})
export class AppModule { }



