import { environment } from "./../../../environments/environment";
import { Injectable, NgZone } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Template } from "./template";
import { ToastService } from "../../../typescripts/pro/alerts";

import { BehaviorSubject, Observable } from "rxjs/Rx";
import { share } from "rxjs/operators";
import { MDBSpinningPreloader } from "../../../typescripts/pro/preloader/preloader.service";

@Injectable()
export class TemplateService {
  public baseUrl = environment.corona.TEMPLATE_BASE_URL;

  public templates: BehaviorSubject<Array<Template>>;
  public tempTemplates: Array<Template>;
  public searchTerm: BehaviorSubject<String>;
  public searchQuery: string;
  public fetchDone: BehaviorSubject<boolean>;

  constructor(
    private http: HttpClient,
    private ngZone: NgZone,
    private mdbSpinningPreloader: MDBSpinningPreloader,
    private toastrService: ToastService
  ) {
    this.tempTemplates = [];
    this.templates = new BehaviorSubject([]);
    this.searchTerm = new BehaviorSubject("");
    this.fetchDone = new BehaviorSubject(false);

    this.searchTerm.subscribe(data => {
      this.searchQuery = data.toLowerCase();
      this.publishTemplateUpdates();
    });

    this.getTemplates().subscribe();
  }

  getTemplates() {
    return new Observable(observer => {
      this.fetchDone.next(false);
      this.ngZone.run(() => {
        this.http.get(this.baseUrl + "/templates").subscribe(data => {
          const templates = data;

          if (templates["templates"]) {
            this.tempTemplates = templates["templates"].map(v => {
              return Object.assign(new Template(), v);
            });
          }
          this.publishTemplateUpdates();
          this.fetchDone.next(true);
        });
      });
    }).share();
  }

  publishTemplateUpdates() {
    this.sort();
    this.templates.next(
      this.tempTemplates.filter(v => {
        return v.name.toLowerCase().indexOf(this.searchQuery) >= 0;
      })
    );
  }

  sort() {
    this.tempTemplates = this.tempTemplates.sort((a, b) => {
      if (a.name < b.name) {
        return -1;
      } else if (a.name > b.name) {
        return 1;
      } else {
        return 0;
      }
    });
  }

  deleteTemplate(template: Template) {
    this.tempTemplates = this.tempTemplates.filter(v => {
      return v.templateId !== template.templateId;
    });

    this.publishTemplateUpdates();

    return this.http
      .delete(this.baseUrl + "/template/" + template.templateId, {
        observe: "response"
      })
      .subscribe(
        data => {
          if (data.status !== 200) {
            this.tempTemplates.push(template);
            this.publishTemplateUpdates();
            this.showError(data.body["message"]);
          }
          this.showSuccess(data.body["message"]);
        },
        error => {
          this.tempTemplates.push(template);
          this.publishTemplateUpdates();
          this.showError(error.error["message"]);
        }
      );
  }

  createOrUpdateTemplate(template: Template) {
    if (template.templateId.length <= 0) {
      delete template.templateId;
      this.ngZone.run(() => {
        this.http.post(this.baseUrl + "/template", template, { observe: "response" }).subscribe(
          data => {
            if (data.status === 200) {
              this.getTemplates().subscribe();
              this.showSuccess(data.body["message"]);
            }
          },
          err => {
            this.showError(err.error["error"]);
          }
        );
      });
    } else {
      this.ngZone.run(() => {
        this.http
          .patch(this.baseUrl + "/template/" + template.templateId, template, {
            observe: "response"
          })
          .subscribe(
            data => {
              if (data.status === 200) {
                this.getTemplates().subscribe();
                this.showSuccess(data.body["message"]);
              }
            },
            err => {
              this.showError(err.error["error"]);
            }
          );
      });
    }
  }

  showSuccess(message) {
    this.toastrService.success(message);
  }

  showError(message) {
    this.toastrService.error(message);
  }
}



