import { NgModule, ModuleWithProviders } from "@angular/core";
import { AgmCoreModule } from "@agm/core";
import { CommonModule } from "@angular/common";
import { FormsModule } from "@angular/forms";
import { HttpClientModule } from "@angular/common/http";
import { NgPipesModule } from "ngx-pipes";
import { MDBBootstrapModule } from "typescripts/free";
import { MDBBootstrapModulePro } from "typescripts/pro";
import { SortTask } from "app/profile/profile-task-sort";
import { SortNotifications } from "app/notification/notification-sort";
import { MaterialSpinnerComponent } from "app/material-spinner/material-spinner.component";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    HttpClientModule,
    NgPipesModule,
    AgmCoreModule,
    MDBBootstrapModule,
    MDBBootstrapModulePro
  ],
  declarations: [MaterialSpinnerComponent, SortTask, SortNotifications],
  exports: [
    MaterialSpinnerComponent,
    SortTask,
    SortNotifications,
    CommonModule,
    FormsModule,
    HttpClientModule,
    NgPipesModule,
    AgmCoreModule,
    MDBBootstrapModule,
    MDBBootstrapModulePro
  ]
})
export class SharedModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: SharedModule
      // providers: [...providers]
    };
  }
}



