import { Injectable } from "@angular/core";

@Injectable()
export class BasicFunctions {
  // search
  public searchQuery;

  // date
  public day: number;
  public month: number;
  public year: number;

  constructor() {
    this.day = 0;
    this.month = 0;
    this.year = 0;
  }

  searchValueChange(q, service) {
    this.searchQuery = q;
    return service.searchTerm.next(this.searchQuery);
  }

  formatMonth(n) {
    if (n === 0) {
      return "01";
    }
    return n < 10 ? "0" + n : "" + n;
  }

  getDateFormat(d: Date, seperator) {
    const day = d.getDate();
    const month = this.formatMonth(d.getMonth());
    const year = d.getFullYear();
    return year + seperator + month + seperator + day;
  }

  formatChar(word) {
    if (word) {
      if (word.length > 15) {
        return word.substr(0, 14) + "...";
      }
      return word;
    }
  }

  validatePrice($event) {
    const allowedKeyCodes = [46, 8, 9, 27, 13, 110, 190];
    if (
      allowedKeyCodes.indexOf($event.keyCode) !== -1 ||
      ($event.keyCode > 47 && $event.keyCode < 58) ||
      // Allow Ctrl+A
      ($event.keyCode === 65 && ($event.ctrlKey || $event.metaKey)) ||
      // Allow Ctrl+C
      ($event.keyCode === 67 && ($event.ctrlKey || $event.metaKey)) ||
      // Allow Ctrl+V
      ($event.keyCode === 86 && ($event.ctrlKey || $event.metaKey)) ||
      // Allow Ctrl+X
      ($event.keyCode === 88 && ($event.ctrlKey || $event.metaKey)) ||
      // Allow home, end, left and right key
      ($event.keyCode >= 35 && $event.keyCode <= 39)
    ) {
      return true;
    }
    return false;
  }

  validatePhone($event) {
    const allowedKeyCodes = [8, 9, 27, 13];
    if (
      allowedKeyCodes.indexOf($event.keyCode) !== -1 ||
      ($event.keyCode > 47 && $event.keyCode < 58) ||
      // Allow Ctrl+A
      ($event.keyCode === 65 && ($event.ctrlKey || $event.metaKey)) ||
      // Allow Ctrl+C
      ($event.keyCode === 67 && ($event.ctrlKey || $event.metaKey)) ||
      // Allow Ctrl+V
      ($event.keyCode === 86 && ($event.ctrlKey || $event.metaKey)) ||
      // Allow Ctrl+X
      ($event.keyCode === 88 && ($event.ctrlKey || $event.metaKey)) ||
      // Allow home, end, left and right key
      ($event.keyCode >= 35 && $event.keyCode <= 39)
    ) {
      return true;
    }
    return false;
  }
}



