import { environment } from "./../../../environments/environment";
import { Injectable, NgZone } from "@angular/core";

import { HttpClient } from "@angular/common/http";

import { Observable } from "rxjs/Observable";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import "rxjs/add/operator/share";

@Injectable()
export class LocationService {
  public locationBaseUrl = environment.corona.LOCATION_BASE_URL;
  public countries: BehaviorSubject<Array<any>>;
  public superCountries: BehaviorSubject<Array<any>>;
  public states: BehaviorSubject<Array<any>>;
  public cities: BehaviorSubject<Array<any>>;

  constructor(private http: HttpClient, private ngZone: NgZone) {
    this.countries = new BehaviorSubject([]);
    this.superCountries = new BehaviorSubject([]);

    this.getCountries().subscribe();

    this.states = new BehaviorSubject([]);
    this.cities = new BehaviorSubject([]);
  }

  getCountries(id = 6255146) {
    return new Observable(observe => {
      this.ngZone.run(() => {
        this.http.get(this.locationBaseUrl + "/continent/" + id + "/countries").subscribe(data => {
          const countries = data["countries"];
          const countriesData = [];
          const superCountriesData = [];

          for (const key of Object.keys(countries)) {
            countriesData.push({ value: key, label: countries[key].name, DialCode: countries[key].DialCode });
            if (countries[key].IsoCode) {
              superCountriesData.push(countries[key]);
            }
          }

          this.publishCountryUpdates(countriesData);
          this.publishCompleteCountryUpdates(superCountriesData);
        });
      });
    });
  }

  publishCountryUpdates(countries) {
    countries = countries.sort((a, b) => {
      if (a.label < b.label) {
        return -1;
      } else if (a.label > b.label) {
        return 1;
      } else {
        return 0;
      }
    });

    this.countries.next(countries);
  }

  publishCompleteCountryUpdates(superCountries) {
    superCountries = superCountries.sort((a, b) => {
      if (a.name < b.name) {
        return -1;
      } else if (a.name > b.name) {
        return 1;
      } else {
        return 0;
      }
    });
    this.superCountries.next(superCountries);
  }

  getStates(id) {
    this.ngZone.run(() => {
      this.http.get(this.locationBaseUrl + "/country/" + id + "/states").subscribe(data => {
        const states = data["states"];
        let statesData = [];

        for (const key of Object.keys(states)) {
          statesData.push({ value: key, label: states[key].name });
        }

        statesData = statesData.sort((a, b) => {
          if (a.label < b.label) {
            return -1;
          } else if (a.label > b.label) {
            return 1;
          } else {
            return 0;
          }
        });

        this.states.next(statesData);
      });
    });
  }

  getCities(id) {
    this.ngZone.run(() => {
      this.http.get(this.locationBaseUrl + "/state/" + id + "/cities").subscribe(data => {
        const cities = data["Locations"];
        let citiesData = [];

        cities.map(city => {
          if (city["id"] !== undefined && city["name"] !== undefined) {
            citiesData.push({ value: city.id, label: city.name });
          } else {
            for (const key of Object.keys(city)) {
              citiesData.push({ value: key, label: city[key].name });
            }
          }
        });

        citiesData = citiesData.sort((a, b) => {
          if (a.label < b.label) {
            return -1;
          } else if (a.label > b.label) {
            return 1;
          } else {
            return 0;
          }
        });
        const newCities = this.removeDuplicates(citiesData, "label");
        this.cities.next(newCities);
      });
    });
  }

  removeDuplicates(myArr, prop) {
    return myArr.filter((obj, pos, arr) => {
      return arr.map(mapObj => mapObj[prop]).indexOf(obj[prop]) === pos;
    });
  }
}


