import { Injectable } from "@angular/core";
import { BehaviorSubject } from "rxjs/BehaviorSubject";

@Injectable()
export class TeamStack {
  private cursor: number;
  public storage: Array<any>;

  public teamMembers: BehaviorSubject<any>;
  public leadTeams: BehaviorSubject<Array<any>>;
  public $hasPrev: BehaviorSubject<Boolean>;
  public $hasNext: BehaviorSubject<Boolean>;

  constructor() {
    this.storage = [];
    this.cursor = -1;
    this.teamMembers = new BehaviorSubject([]);
    this.leadTeams = new BehaviorSubject([]);
    this.$hasPrev = new BehaviorSubject(false);
    this.$hasNext = new BehaviorSubject(false);
  }

  public push(teamDetailsObj) {
    if (this.hasNext()) {
      this.storage.splice(this.cursor + 1, this.size());
      this.publishHasNext(false);
    }
    this.storage.push(teamDetailsObj);
    this.cursor++;
    const data = this.storage[this.cursor];
    this.leadTeams.next(data.leadTeams);
    this.teamMembers.next(data.teamMembers);
    if (this.hasPrev()) {
      this.publishHasPrev(true);
    }
  }

  public reset() {
    this.storage = [];
    this.cursor = -1;
    this.teamMembers = new BehaviorSubject([]);
    this.$hasPrev = new BehaviorSubject(false);
    this.$hasNext = new BehaviorSubject(false);
    this.leadTeams = new BehaviorSubject([]);
  }

  public size() {
    return this.storage.length;
  }

  public hasNext() {
    return this.cursor < this.size() - 1;
  }

  public publishHasNext(value) {
    this.$hasNext.next(value);
  }

  public hasPrev() {
    return this.cursor > 0;
  }

  public publishHasPrev(value) {
    this.$hasPrev.next(value);
  }

  public next() {
    if (this.hasNext()) {
      this.cursor++;
      if (!this.hasNext()) {
        this.publishHasNext(false);
      }
      const data = this.storage[this.cursor];
      this.leadTeams.next(data.leadTeams);
      this.teamMembers.next(data.teamMembers);
      this.publishHasPrev(true);
    }
    return;
  }

  public prev() {
    if (this.hasPrev()) {
      this.cursor--;
      if (!this.hasPrev()) {
        this.publishHasPrev(false);
      }
      const data = this.storage[this.cursor];
      this.leadTeams.next(data.leadTeams);
      this.teamMembers.next(data.teamMembers);
      this.publishHasNext(true);
    }
    return;
  }
}



