export class Team {
  teamId: string;
  teamName: string;
  userId: string;
  country: string;
  region: string;
  state: string;
  teamMembers: Array<any>;
  teamLead: string;
  areaOfOperation: string;
  deletedAt: string;

  constructor() {
    this.teamId = "";
    this.teamName = "";
    this.userId = "";
    this.country = "";
    this.region = "";
    this.state = "";
    this.teamLead = "";
    this.areaOfOperation = "";
    this.deletedAt = "";
    this.teamMembers = [];
  }
}



