import { Router } from "@angular/router";
import { Component, OnInit } from "@angular/core";
import { BehaviorSubject } from "rxjs/BehaviorSubject";

import { UserService } from "../../shared/user/user.service";
import { AuthService } from "../../shared/user/auth.service";
import { SideNavService } from "../../shared/sidenav.service";

import { Notification } from "../../shared/notification/notification";
import { NotificationService } from "../../shared/notification/notification.service";

import { User } from "../../shared/user/user";

import * as moment from "moment";

@Component({
  selector: "app-logged-in-navbar",
  templateUrl: "./logged-in-navbar.component.html",
  styleUrls: ["./logged-in-navbar.component.scss"]
})
export class LoggedInNavbarComponent implements OnInit {
  public loggedUser;
  public userPicture;
  public readNoteCount: any;
  public notifications: BehaviorSubject<Array<Notification>>;
  public noteCount: number;

  constructor(
    private userService: UserService,
    private authService: AuthService,
    private sideNavService: SideNavService,
    private notificationService: NotificationService,
    private router: Router
  ) {
    this.readNoteCount = 0;
    this.notifications = this.notificationService.notifications;
  }

  ngOnInit() {
    if (this.authService.getUserId()) {
      const loggedInUserId = this.authService.getUserId();

      this.userService.getUser(loggedInUserId).subscribe(res => {
        this.loggedUser = res["data"].firstName;
        this.userPicture = res["data"].profilePicture;
      });
    }

    this.userService.ImageUrl.subscribe(value => {
      this.userPicture = value;
    });

    this.notifications.subscribe(data => {
      this.noteCount = data.length;
      this.readNoteCount = data.filter(note => !note.isRead).length;
    });
  }

  readNotification(notification) {
    this.notificationService.readNotification(notification);
    const link = notification.subject.split("-")[0];
    this.router.navigate([`/home/tasks/${link}`], { queryParams: { id: notification.subjectId } });
  }

  toggleSideNav() {
    this.sideNavService.sidenav.next(true);
  }

  getUserNameByUserId(userId) {
    return this.userService.getUserNameByUserId(userId);
  }

  friendlyDate(date) {
    return moment(date).fromNow();
  }
}



